import React, { useState } from 'react';
import { MoreVertical, Phone, Video, Users, Hash, MessageCircle } from 'lucide-react';
import { Conversation } from '../../stores/chatStore';
import { useAuth } from '../../contexts/AuthContext';

interface ChatHeaderProps {
  conversation: Conversation;
  isConnected: boolean;
}

const ChatHeader: React.FC<ChatHeaderProps> = ({ conversation, isConnected }) => {
  const { user } = useAuth();
  const [showMenu, setShowMenu] = useState(false);

  const getConversationTitle = () => {
    if (conversation.name) return conversation.name;
    
    if (conversation.type === 'direct') {
      const otherParticipant = conversation.participants.find(p => p._id !== user?.id);
      return otherParticipant?.username || 'Unknown User';
    }
    
    return 'Unnamed Conversation';
  };

  const getConversationIcon = () => {
    switch (conversation.type) {
      case 'direct':
        return <MessageCircle className="w-5 h-5" />;
      case 'group':
        return <Users className="w-5 h-5" />;
      case 'channel':
        return <Hash className="w-5 h-5" />;
      default:
        return <MessageCircle className="w-5 h-5" />;
    }
  };

  const getParticipantCount = () => {
    if (conversation.type === 'direct') {
      return 'Direct message';
    }
    return `${conversation.participants.length} members`;
  };

  const getConnectionStatus = () => {
    if (!isConnected) {
      return <span className="text-red-500 text-xs">Disconnected</span>;
    }
    return <span className="text-green-500 text-xs">Connected</span>;
  };

  return (
    <div className="border-b border-gray-200 p-4 bg-white">
      <div className="flex items-center justify-between">
        <div className="flex items-center space-x-3">
          {/* Avatar/Icon */}
          <div className="flex-shrink-0">
            {conversation.type === 'direct' ? (
              <div className="w-10 h-10 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full flex items-center justify-center text-white font-medium">
                {getConversationTitle().charAt(0).toUpperCase()}
              </div>
            ) : (
              <div className="w-10 h-10 bg-gray-200 rounded-lg flex items-center justify-center text-gray-600">
                {getConversationIcon()}
              </div>
            )}
          </div>

          {/* Title and Info */}
          <div className="flex-1 min-w-0">
            <div className="flex items-center space-x-2">
              <h2 className="text-lg font-semibold text-gray-900 truncate">
                {getConversationTitle()}
              </h2>
              {getConnectionStatus()}
            </div>
            <p className="text-sm text-gray-500 truncate">
              {getParticipantCount()}
            </p>
          </div>
        </div>

        {/* Actions */}
        <div className="flex items-center space-x-2">
          {/* Video/Audio call buttons (placeholder) */}
          <button className="p-2 text-gray-500 hover:text-gray-700 hover:bg-gray-100 rounded-lg transition-colors">
            <Phone className="w-5 h-5" />
          </button>
          <button className="p-2 text-gray-500 hover:text-gray-700 hover:bg-gray-100 rounded-lg transition-colors">
            <Video className="w-5 h-5" />
          </button>

          {/* More options */}
          <div className="relative">
            <button
              onClick={() => setShowMenu(!showMenu)}
              className="p-2 text-gray-500 hover:text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
            >
              <MoreVertical className="w-5 h-5" />
            </button>

            {showMenu && (
              <div className="absolute right-0 mt-2 w-48 bg-white rounded-lg shadow-lg border border-gray-200 py-1 z-10">
                <button className="w-full px-4 py-2 text-left text-sm text-gray-700 hover:bg-gray-100">
                  View Profile
                </button>
                <button className="w-full px-4 py-2 text-left text-sm text-gray-700 hover:bg-gray-100">
                  Mute Notifications
                </button>
                <button className="w-full px-4 py-2 text-left text-sm text-gray-700 hover:bg-gray-100">
                  Search Messages
                </button>
                {conversation.type !== 'direct' && (
                  <>
                    <hr className="my-1" />
                    <button className="w-full px-4 py-2 text-left text-sm text-gray-700 hover:bg-gray-100">
                      Add Members
                    </button>
                    <button className="w-full px-4 py-2 text-left text-sm text-gray-700 hover:bg-gray-100">
                      Edit Group
                    </button>
                  </>
                )}
                <hr className="my-1" />
                <button className="w-full px-4 py-2 text-left text-sm text-red-600 hover:bg-red-50">
                  Leave Conversation
                </button>
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Description (for groups/channels) */}
      {conversation.description && (
        <div className="mt-2">
          <p className="text-sm text-gray-600">{conversation.description}</p>
        </div>
      )}
    </div>
  );
};

export default ChatHeader;
