import React, { useEffect } from 'react';
import { motion } from 'framer-motion';
import { Layout } from '@/components/Layout/Layout';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/Card';
import { Badge } from '@/components/ui/Badge';
import { Button } from '@/components/ui/Button';
import { useAuth } from '@/contexts/AuthContext';
import { formatDate, getInitials } from '@/lib/utils';
import { 
  Shield, 
  Smartphone, 
  Clock, 
  CheckCircle, 
  Plus,
  Settings,
  Activity,
  MessageCircle
} from 'lucide-react';
import { Link } from 'react-router-dom';

export const Dashboard: React.FC = () => {
  const { user, devices, sessions, loadDevices, loadSessions } = useAuth();

  useEffect(() => {
    loadDevices();
    loadSessions();
  }, [loadDevices, loadSessions]);

  if (!user) {
    return null;
  }

  const stats = [
    {
      title: 'Registered Devices',
      value: devices.length,
      icon: Smartphone,
      color: 'text-primary-600',
      bgColor: 'bg-primary-100',
    },
    {
      title: 'Active Sessions',
      value: sessions.filter(s => s.isCurrent).length,
      icon: Activity,
      color: 'text-success-600',
      bgColor: 'bg-success-100',
    },
    {
      title: 'Account Age',
      value: Math.floor((Date.now() - new Date(user.createdAt).getTime()) / (1000 * 60 * 60 * 24)),
      icon: Clock,
      color: 'text-warning-600',
      bgColor: 'bg-warning-100',
      suffix: 'days',
    },
    {
      title: 'Security Status',
      value: user.hasAuthenticators ? 'Secure' : 'Setup Required',
      icon: Shield,
      color: user.hasAuthenticators ? 'text-success-600' : 'text-error-600',
      bgColor: user.hasAuthenticators ? 'bg-success-100' : 'bg-error-100',
    },
  ];

  return (
    <Layout>
      <div className="container mx-auto px-4 py-8">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          className="mb-8"
        >
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <div className="flex h-16 w-16 items-center justify-center rounded-full bg-primary-100 text-2xl font-bold text-primary-700">
                {getInitials(user.displayName)}
              </div>
              <div>
                <h1 className="text-3xl font-bold text-secondary-900">
                  Welcome back, {user.displayName}!
                </h1>
                <p className="text-secondary-600">
                  Here's your security dashboard
                </p>
              </div>
            </div>
            <Badge variant={user.hasAuthenticators ? 'success' : 'error'}>
              {user.hasAuthenticators ? (
                <>
                  <CheckCircle className="h-3 w-3 mr-1" />
                  Secure
                </>
              ) : (
                'Setup Required'
              )}
            </Badge>
          </div>
        </motion.div>

        {/* Stats Grid */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.1 }}
          className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8"
        >
          {stats.map((stat) => (
            <Card key={stat.title} className="hover:shadow-medium transition-shadow">
              <CardContent className="p-6">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-sm font-medium text-secondary-600">{stat.title}</p>
                    <p className="text-2xl font-bold text-secondary-900">
                      {stat.value}
                      {stat.suffix && <span className="text-sm text-secondary-500 ml-1">{stat.suffix}</span>}
                    </p>
                  </div>
                  <div className={`flex h-12 w-12 items-center justify-center rounded-lg ${stat.bgColor}`}>
                    <stat.icon className={`h-6 w-6 ${stat.color}`} />
                  </div>
                </div>
              </CardContent>
            </Card>
          ))}
        </motion.div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          {/* Recent Activity */}
          <motion.div
            initial={{ opacity: 0, x: -20 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.6, delay: 0.2 }}
          >
            <Card>
              <CardHeader>
                <div className="flex items-center justify-between">
                  <div>
                    <CardTitle>Recent Activity</CardTitle>
                    <CardDescription>Your latest security events</CardDescription>
                  </div>
                  <Button variant="ghost" size="sm">
                    <Settings className="h-4 w-4" />
                  </Button>
                </div>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  <div className="flex items-center space-x-3 p-3 rounded-lg bg-success-50">
                    <div className="flex h-8 w-8 items-center justify-center rounded-full bg-success-100">
                      <CheckCircle className="h-4 w-4 text-success-600" />
                    </div>
                    <div className="flex-1">
                      <p className="text-sm font-medium text-secondary-900">Successful login</p>
                      <p className="text-xs text-secondary-500">Just now</p>
                    </div>
                  </div>

                  {user.lastLoginAt && (
                    <div className="flex items-center space-x-3 p-3 rounded-lg bg-secondary-50">
                      <div className="flex h-8 w-8 items-center justify-center rounded-full bg-secondary-100">
                        <Shield className="h-4 w-4 text-secondary-600" />
                      </div>
                      <div className="flex-1">
                        <p className="text-sm font-medium text-secondary-900">Last login</p>
                        <p className="text-xs text-secondary-500">{formatDate(user.lastLoginAt)}</p>
                      </div>
                    </div>
                  )}

                  <div className="flex items-center space-x-3 p-3 rounded-lg bg-primary-50">
                    <div className="flex h-8 w-8 items-center justify-center rounded-full bg-primary-100">
                      <Plus className="h-4 w-4 text-primary-600" />
                    </div>
                    <div className="flex-1">
                      <p className="text-sm font-medium text-secondary-900">Account created</p>
                      <p className="text-xs text-secondary-500">{formatDate(user.createdAt)}</p>
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>
          </motion.div>

          {/* Quick Actions */}
          <motion.div
            initial={{ opacity: 0, x: 20 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.6, delay: 0.3 }}
          >
            <Card>
              <CardHeader>
                <CardTitle>Quick Actions</CardTitle>
                <CardDescription>Manage your security settings</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  <Link to="/chat">
                    <Button className="w-full justify-start" variant="outline">
                      <MessageCircle className="h-4 w-4 mr-2" />
                      Open Nexus Chat
                    </Button>
                  </Link>
                  <Button className="w-full justify-start" variant="outline">
                    <Smartphone className="h-4 w-4 mr-2" />
                    Manage Devices
                  </Button>
                  <Button className="w-full justify-start" variant="outline">
                    <Activity className="h-4 w-4 mr-2" />
                    View Sessions
                  </Button>
                  <Button className="w-full justify-start" variant="outline">
                    <Settings className="h-4 w-4 mr-2" />
                    Security Settings
                  </Button>
                  {!user.hasAuthenticators && (
                    <Button className="w-full justify-start">
                      <Plus className="h-4 w-4 mr-2" />
                      Add Security Device
                    </Button>
                  )}
                </div>
              </CardContent>
            </Card>
          </motion.div>
        </div>

        {/* Security Recommendations */}
        {!user.hasAuthenticators && (
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: 0.4 }}
            className="mt-8"
          >
            <Card className="border-warning-200 bg-warning-50">
              <CardHeader>
                <CardTitle className="text-warning-900">Security Setup Required</CardTitle>
                <CardDescription className="text-warning-700">
                  Complete your security setup to protect your account
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  <p className="text-sm text-warning-800">
                    Your account is not fully secured yet. Add a security device to enable passwordless login.
                  </p>
                  <Button className="bg-warning-600 hover:bg-warning-700">
                    <Plus className="h-4 w-4 mr-2" />
                    Add Security Device
                  </Button>
                </div>
              </CardContent>
            </Card>
          </motion.div>
        )}
      </div>
    </Layout>
  );
};
