import React, { useState, useRef, useCallback } from 'react';
import { Send, Paperclip, Smile } from 'lucide-react';
import { useDropzone } from 'react-dropzone';
import { api } from '../../lib/api';

interface MessageInputProps {
  onSendMessage: (content: string, type: 'text' | 'image' | 'file', fileInfo?: any) => void;
  onTyping: (isTyping: boolean) => void;
  disabled?: boolean;
}

const MessageInput: React.FC<MessageInputProps> = ({
  onSendMessage,
  onTyping,
  disabled = false
}) => {
  const [message, setMessage] = useState('');
  const [isUploading, setIsUploading] = useState(false);
  const textareaRef = useRef<HTMLTextAreaElement>(null);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (message.trim() && !disabled) {
      onSendMessage(message, 'text');
      setMessage('');
      onTyping(false);
    }
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const value = e.target.value;
    setMessage(value);
    
    if (value.trim()) {
      onTyping(true);
    } else {
      onTyping(false);
    }
  };

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSubmit(e);
    }
  };

  const handleFileUpload = async (files: File[]) => {
    if (files.length === 0 || disabled) return;

    const file = files[0];
    setIsUploading(true);

    try {
      // Get pre-signed URL
      const signResponse = await api.post('/upload/sign-url', {
        fileName: file.name,
        fileType: file.type,
        fileSize: file.size
      });

      if (signResponse.data.success) {
        const { uploadUrl, fileKey, publicUrl } = signResponse.data.data;

        // Upload file to S3
        const uploadResponse = await fetch(uploadUrl, {
          method: 'PUT',
          body: file,
          headers: {
            'Content-Type': file.type,
          },
        });

        if (uploadResponse.ok) {
          const fileInfo = {
            filename: fileKey,
            originalName: file.name,
            mimeType: file.type,
            size: file.size,
            url: publicUrl
          };

          const messageType = file.type.startsWith('image/') ? 'image' : 'file';
          onSendMessage('', messageType, fileInfo);
        } else {
          throw new Error('Upload failed');
        }
      }
    } catch (error) {
      console.error('File upload failed:', error);
      // Fallback to direct upload
      try {
        const formData = new FormData();
        formData.append('file', file);

        const response = await api.post('/upload/direct', formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        });

        if (response.data.success) {
          const messageType = file.type.startsWith('image/') ? 'image' : 'file';
          onSendMessage('', messageType, response.data.data.fileInfo);
        }
      } catch (fallbackError) {
        console.error('Fallback upload failed:', fallbackError);
        alert('File upload failed. Please try again.');
      }
    } finally {
      setIsUploading(false);
    }
  };

  const onDrop = useCallback(handleFileUpload, [disabled, onSendMessage]);

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept: {
      'image/*': ['.jpeg', '.jpg', '.png', '.gif', '.webp'],
      'video/*': ['.mp4', '.webm', '.ogg'],
      'application/pdf': ['.pdf'],
      'application/msword': ['.doc'],
      'application/vnd.openxmlformats-officedocument.wordprocessingml.document': ['.docx'],
      'text/plain': ['.txt'],
    },
    maxFiles: 1,
    maxSize: 50 * 1024 * 1024, // 50MB
  });

  const getFileIcon = () => {
    if (isUploading) {
      return <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-blue-600"></div>;
    }
    return <Paperclip className="w-5 h-5" />;
  };

  return (
    <form onSubmit={handleSubmit} className="flex items-end space-x-2">
      {/* File Upload Area */}
      <div
        {...getRootProps()}
        className={`p-2 border-2 border-dashed rounded-lg cursor-pointer transition-colors ${
          isDragActive
            ? 'border-blue-400 bg-blue-50'
            : 'border-gray-300 hover:border-gray-400'
        } ${disabled ? 'opacity-50 cursor-not-allowed' : ''}`}
      >
        <input {...getInputProps()} />
        {getFileIcon()}
      </div>

      {/* Message Input */}
      <div className="flex-1 relative">
        <textarea
          ref={textareaRef}
          value={message}
          onChange={handleInputChange}
          onKeyDown={handleKeyDown}
          placeholder="Type a message..."
          disabled={disabled}
          className="w-full px-4 py-3 pr-12 border border-gray-300 rounded-lg resize-none focus:ring-2 focus:ring-blue-500 focus:border-transparent disabled:opacity-50 disabled:cursor-not-allowed"
          rows={1}
          style={{
            minHeight: '48px',
            maxHeight: '120px',
            height: 'auto',
          }}
          onInput={(e) => {
            const target = e.target as HTMLTextAreaElement;
            target.style.height = 'auto';
            target.style.height = `${Math.min(target.scrollHeight, 120)}px`;
          }}
        />
        
        {/* Emoji Button */}
        <button
          type="button"
          className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-600 transition-colors"
          disabled={disabled}
        >
          <Smile className="w-5 h-5" />
        </button>
      </div>

      {/* Send Button */}
      <button
        type="submit"
        disabled={!message.trim() || disabled || isUploading}
        className="p-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
      >
        <Send className="w-5 h-5" />
      </button>
    </form>
  );
};

export default MessageInput;
